import React, { Component } from 'react'
import styled from 'react-emotion'
import logoRed from '../assets/logo-red.svg'
import logo from '../assets/logo-white.svg'
import Link from '../components/Link'
import { borders, colors, layout, transitions } from '../theme'

const StyledNavbar = styled.nav`
  position: fixed;
  width: 100%;
  z-index: 2;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.5s all;
  background: ${colors.brand.inverted};
  color: ${colors.brand.normal};
  border-bottom: 1px solid #f5ebe9;

  .wrapper {
    height: 62px;
    max-width: ${layout.maxWidth};
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px !important;
    font-size: 1.2em;
    font-weight: 700;
  }

  .logo {
    height: 24px;
    display: block;
  }
  .phone {
    line-height: 20px;
    text-align: right;
  }
  .CTA {
    display: ${props => (props.noCta ? 'none' : 'block')};
    padding: 5px 7px;
    margin-left: auto;
    margin-right: 14px;
    font-size: 0.7em;
    color: ${colors.brand.normal};
    text-align: center;
    text-transform: uppercase;
    background: transparent;
    border: 1px solid ${colors.brand.normal};
    border-radius: ${borders.radius.small};
    outline: none;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    transition: ${transitions.all};

    &:hover {
      background: #fc4c4c;
      color: white;
    }
  }

  &.scrolled {
    background: white;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    color: ${colors.brand.normal};

    .CTA {
      display: ${props => (props.noCta ? 'none' : 'block')};
      opacity: 1;
      pointer-events: all;
    }
  }

  .blog-link {
    margin-left: 30px;
    margin-right: auto;
    font-weight: normal;
    font-size: 1rem;
  }

  .link {
    position: relative;

    &::after {
      content: ' ';
      position: absolute;
      width: 50%;
      height: 4px;
      border-radius: 2px;
      background-color: #fc4c4c;
      bottom: -5px;
      left: 50%;
      margin-left: -25%;
      opacity: 0;
      transition: all 150ms ease-in-out;
    }
  }

  .link:hover::after {
    opacity: 1;
  }

  @media screen and (max-width: 768px) {
    .wrapper {
      padding: 16px 20px !important;
    }
    .CTA {
      display: none !important;
    }
    .logo {
      height: 15px;
    }
  }
`
function scrollToCTA() {
  window.scroll({
    top: 0,
    behavior: 'smooth',
  })
}

class Navbar extends Component {
  componentDidMount() {
    let navbar = document.querySelector('#navbar')

    document.addEventListener('scroll', () => {
      if (!navbar) {
        return
      }
      if ((window.pageYOffset > 100) & !navbar.classList.contains('scrolled')) {
        navbar.classList.add('scrolled')
      }
      if ((window.pageYOffset <= 100) & navbar.classList.contains('scrolled')) {
        navbar.classList.remove('scrolled')
      }
    })
  }

  render() {
    const redLogo = (
      <img
        className="logo logo-red"
        src={logoRed}
        alt="Logo de monbanquet.fr"
      />
    )

    return (
      <StyledNavbar
        id="navbar"
        className={this.props.inverted ? 'inverted' : ''}
        noCta={this.props.noCta}
      >
        <div className="wrapper">
          {this.props.noLink ? (
            <div>{redLogo}</div>
          ) : (
            <Link to="/">{redLogo}</Link>
          )}
          <Link className="CTA" to="/commande">
            Demander un devis
          </Link>
          <a className="phone" href="tel: 01 76 44 04 41">
            01 76 44 04 41
          </a>
        </div>
      </StyledNavbar>
    )
  }
}

export default Navbar
export { StyledNavbar }



