import Link from '../components/Link'
import addToMailchimp from 'gatsby-plugin-mailchimp'
import React from 'react'
import Emoji from 'react-emoji-render'
import styled from 'react-emotion'
import { constants } from '../constants'
import { borders, colors, layout } from '../theme'

const StyledFooter = styled.footer`
  hr {
    border-top: 1px solid ${borders.color.light};
    height: 0;
  }

  .wrapper {
    max-width: ${layout.maxWidth};
    margin: 0 auto;
    padding: ${layout.sectionPadding} ${layout.containerPadding.normal};
  }

  a {
    transition: all ease-in-out 150ms;
    &:hover {
      color: ${colors.brand.normal};
    }
  }

  .main-row {
    display: flex;
    justify-content: space-between;
  }

  .row {
    display: flex;
    min-height: 130px;
  }

  .emoji {
    margin-top: -4px;
    font-size: 1.2rem;
  }

  ul {
    padding: 0;
  }

  .footer-section {
    display: flex;
    flex-direction: column;
    margin-left: 12px;
    width: inherit;

    .title {
      color: ${colors.text.light};
      font-weight: 600;
    }
    .phone {
      margin-top: 10px;
      color: ${colors.brand.dark};
      font-weight: 600;
    }
    .newsletter-field {
      display: flex;
      margin-top: 8px;
      .email {
        padding: 8px 16px;
        border: 1px solid ${borders.color.light};
        border-top-left-radius: ${borders.radius.small};
        border-bottom-left-radius: ${borders.radius.small};
        border-right: none;
        color: ${colors.brand.dark2};
        outline: none;
        width: 100%;
      }
    }

    .hint {
      font-size: 0.9rem;
      max-width: 350px;
      color: ${colors.text.light};
      margin-top: 5px;
      max-width: 300px;
    }

    li {
      list-style: none;
      margin-bottom: 15px;
      color: ${colors.text.light};
      font-size: 0.9rem;
    }
  }

  .signup {
    cursor: pointer;
    background: #fff;
    border: 1px solid ${borders.color.light};
    text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.3);
    border-top-right-radius: ${borders.radius.small};
    border-bottom-right-radius: ${borders.radius.small};
    padding: 0 12px;
    font-size: 1rem;
    transition: all ease-in-out 150ms;

    &:hover {
      border-color: ${colors.brand.normal};
      text-shadow: 3px 4px 2px rgba(0, 0, 0, 0.4);
    }
  }

  .cgv {
    color: ${colors.text.light};
    font-size: 0.9rem;
    margin-top: -39px;

    .emoji {
      margin-right: 10px;
    }
  }

  @media screen and (max-width: 768px) {
    .main-row {
      flex-wrap: wrap;
    }
    .row {
      margin-bottom: 35px;
      min-height: 0;
      width: 100%;
    }
    .cgv {
      margin-top: 0;
    }
    .footer-section .email,
    .footer-section .signup {
      margin-top: 10px;
      height: 40px;
    }
  }
`

class Footer extends React.Component {
  constructor() {
    super()
    this.state = {
      email: ``,
      hint: null,
    }
  }

  _handleEmailChange = e => {
    this.setState({ email: e.target.value })
  }

  _handleSubmit = e => {
    e.preventDefault()
    e.stopPropagation()

    addToMailchimp(this.state.email)
      .then(data => {
        let msg
        if (
          data.result === 'success' ||
          data.msg.indexOf('is already subscribed') !== -1
        ) {
          msg = 'Promis on pensera  vous'
        } else if (
          data.msg.indexOf('too many recent signup requests') !== -1 ||
          data.msg.indexOf(
            'Too many subscribe attempts for this email address',
          ) !== -1
        ) {
          msg =
            'Ok, ok vous tes enthousiastes ! Et bien voici un code Promo pour vous: SIGNMEUP'
        } else {
          msg = 'Oops re-essayez'
        }
        this.setState({
          ...this.state,
          ...{ hint: msg },
        })
      })
      .catch(() => {})
  }
  render() {
    let hintElement

    if (this.state.hint) {
      hintElement = <p className="hint">{this.state.hint}</p>
    }

    return (
      <StyledFooter>
        <hr />
        <div className="wrapper">
          <div className="main-row">
            <div className="row">
              <Emoji className="emoji" text="" />
              <div className="footer-section">
                <p className="title">Appelez-nous</p>
                <a className="phone" href="tel:01 76 44 04 41">
                  01 76 44 04 41
                </a>
              </div>
            </div>
            <div className="row">
              <Emoji className="emoji" text="" />
              <div className="footer-section">
                <p className="title">Suivez notre actualit</p>
                <form
                  className="newsletter-field"
                  onSubmit={this._handleSubmit}
                >
                  <input
                    className="email"
                    type="email"
                    placeholder="votre@adresse.mail"
                    onChange={this._handleEmailChange}
                  />
                  <button className="signup" type="submit">
                    <Emoji className="emoji" text="" />
                  </button>
                </form>
                {hintElement}
              </div>
            </div>
            <div className="row">
              <Emoji className="emoji" text="" />
              <ul className="footer-section">
                <li>
                  <a
                    href={constants.jobPageLink}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    On recrute !
                  </a>
                </li>
                <li>
                  <a
                    href={constants.blogPageLink}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Blog
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div className="cgv">
            <Emoji className="emoji" text="" />
            <Link to="/cgv">C.G.V</Link> {' '}
            <Link to="/legal">Mentions Lgales</Link>
          </div>
        </div>
      </StyledFooter>
    )
  }
}

export default Footer
export { StyledFooter }



