import React from 'react'
import GatsbyLink, { navigateTo } from 'gatsby-link'
import { LocationContext } from '../context'

const Link = ({ keepQueryParams = true, ...props }) => {
  return (
    <LocationContext.Consumer>
      {context => {
        if (!props.to) {
          return <div {...props}>{props.children}</div>
        }
        if (keepQueryParams) {
          props.to = `${props.to}${context.search}`
        }
        return <GatsbyLink {...props}>{props.children}</GatsbyLink>
      }}
    </LocationContext.Consumer>
  )
}

export default Link



