import Link from '../components/Link'
import React from 'react'
import styled from 'react-emotion'
import { borders, colors, layout, shadows, transitions } from '../theme'

const StyledBanquetForAll = styled.section`
  max-width: ${layout.maxWidth};
  margin: 50px auto;
  padding: 20px ${layout.containerPadding.normal};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h3.title {
    font-weight: 300;
    font-size: 2.3rem;
    max-width: 800px;
    color: ${colors.text.normal};
    margin-bottom: 20px;
    text-align: center;
    margin-bottom: 40px;
  }

  .button {
    cursor: pointer;
    background: ${colors.brand.normal};
    background-image: linear-gradient(270deg, #fb3253 0, #fc4c4c 100%);
    border: none;
    border-radius: ${borders.radius.small};
    padding: 10px 25px;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    font-weight: 900;
    color: ${colors.brand.inverted};
    box-shadow: ${shadows.both};
    transition: ${transitions.all};

    &:hover {
      box-shadow: ${shadows.bothHigh};
    }
  }

  p {
    text-align: center;
    font-size: 1rem;
    margin-top: 15px;
    color: ${colors.text.light2};
  }
`

const BanquetForAll = ({
  buttonLabel = 'Demander un devis',
  link = '/commande',
  onClick,
  children,
}) => (
  <StyledBanquetForAll>
    {children && <h3 className="title">{children}</h3>}
    {!onClick ? (
      <Link className="button" to={link}>
        {buttonLabel}
      </Link>
    ) : (
      <button className="button" onClick={() => onClick()}>
        {buttonLabel}
      </button>
    )}
    <p>Votre vnement sur-mesure</p>
  </StyledBanquetForAll>
)

export default BanquetForAll
export { StyledBanquetForAll }



